import i18n from 'i18next';
import {initReactI18next} from 'react-i18next';
import LanguageDetector from 'i18next-browser-languagedetector';

i18n
    .use(initReactI18next)
    .use(LanguageDetector)
    .init({
        fallbackLng: 'en',
        debug: true,
        interpolation: {
            escapeValue: false, // not needed for react as it escapes by default
        },
        resources: {
            en: {
                translation: {
                    "Main": "Main",
                    "News": "news",
                    "Profile": "Profile",
                    "Logout": "Logout",
                    'Favorite movie': 'Favorite movie',
                    "There's nothing here yet": "There's nothing here yet ...",
                    "Log In": "Log In",
                    "Details": 'Details',
                    "To Favorite": "To Favorite",
                    "Delete from favorite": "Delete from favorite",
                    "Back": "Back",
                    "Popular films": "Popular films",
                    "Top rated movies": "Top rated movies",
                    "Oops ... Something went wrong. You can go back to the main": "Oops ... Something went wrong. You can go back to the main",
                    "Back to main": "Back to main",
                    "Wrong login or password": "Wrong login or password",
                    "Login": "login",
                    "Password": "Password",

                }
            },
            ua: {
                translation: {
                    "Main": "",
                    "News": "",
                    "Profile": "",
                    "Logout": "",
                    'Favorite movie': ' ',
                    "There's nothing here yet": "     ...",
                    "Log In": "",
                    "Details": "",
                    "To Favorite": " ",
                    "Delete from favorite": "  ",
                    "Back": "",
                    "Popular films": " ",
                    "Top rated movies": "  ",
                    "Oops ... Something went wrong. You can go back to the main": " ...     ,  .     ",
                    "Back to main": " ",
                    "Wrong login or password": "   ",
                    "Login": "",
                    "Password": "",
                }
            }
        }
    });

export default i18n;